// 展示组件页面
<template>
  <div id="index">
    <header1 class="topbar"></header1>
    <section class="container">
      <div class="left_side">
        <mainLeft></mainLeft>
      </div>
      <div class="main_wrapper">
        <navigator class="nav"></navigator>
        <router-view></router-view>
      </div>
    </section>
  </div>
</template>

<script>
import header from '@/components/common/header'
import mainLeft from '@/components/common/mainLeft'
import navigator from '@/components/common/navigator'
export default {
  components:{
    header1: header,
    mainLeft: mainLeft,
    navigator:navigator
  },
  data() {
    return {
      username: '许如梦'
    }
  },
  created(){
    this.init()
  },
  methods: {
    init(){

      let ws=new WebSocket(this.$websocketUrl,JSON.parse(sessionStorage.getItem("token")).val)
              //接收数据
              ws.onmessage=(msg)=>{
                let data=JSON.parse(msg.data)
                if(data.message==='is_lock')
                  {
                    // this.$message({
                    //   message: '有人在登录',
                    //   type: 'success'
                    // })
                    this.open()
                  }
                // console.log(data)
                // if(data.type==='被登录')
                //   console.log(data.message)
              }
    },
    open() {
        this.$confirm('系统检测到有人在登录你的账号, 是否退出?', '提示', {
          confirmButtonText: '退出',
          cancelButtonText: '取消',
          type: 'warning'
        }).then(() => {
          // this.$message({
          //   type: 'success',

          // });
          this.exit()
        }).catch(() => {
          this.$message({
            type: 'success',
            message: '已取消'
          });
        });
      }, exit() {  //退出登录


      this.$axios({
        url: '/api/logout/'+this.user.studentId,
        method: 'put'
      }).then(res => {
        console.log(res)
        if(res.data.code ==200) {
          this.$router.push({path:"/"}) //跳转到登录页面
          this.$cookies.remove("cname") //清除cookie
          sessionStorage.removeItem("token")
          this.$cookies.remove("image")
          this.$cookies.remove("cid")
          
      this.$cookies.remove("activeInfo")
      this.$cookies.remove("index")
      this.$cookies.remove("now")
          this.$message({
            message: '退出成功',
            type: 'success'
          })
        }
      })

    },
  }
}
</script>

<style lang="less" scoped>
#index .nav {
  box-shadow: 1px 0 5px rgba(0, 0, 0, 0.1);
  margin-bottom: 30px;
}
.container {
  display: flex;
  background-color: #fff;
}
.main_wrapper {
  overflow: hidden;
  flex: 1;
  background-color: #fff;
}
</style>




