<template>
  <div id="nav">
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item class="title" ><a :href="link"><span style="font-weight: bold;" @click="toHome">
      <i class="el-icon-s-home"></i>  首页</span></a>
      </el-breadcrumb-item>
      <el-breadcrumb-item class="title" v-if="active.title!='首页'"><span style="font-weight: bold;">
      <i class="iconfont" :class="icons"></i>  {{active.title}}</span>
      </el-breadcrumb-item>

      <el-breadcrumb-item v-if="now ==0 " style="padding-top: 1px;">{{active.content[now].title}}</el-breadcrumb-item>
      <el-breadcrumb-item v-if="now ==1 " style="padding-top: 1px;">{{active.content[now].title}}</el-breadcrumb-item>
      <el-breadcrumb-item v-if="now ==2 " style="padding-top: 1px;">{{active.content[now].title}}</el-breadcrumb-item>
    </el-breadcrumb>
  </div>
</template>

<script>
import {mapState} from 'vuex'
export default {
  data() {
    return {
      active: [],
      index1: null,
      now:null,
      icons:null,
      link:'/index#/index'
    }
  },
  computed: mapState(["menu"]),
  methods: {
    getIndex() {
      this.bus.$on('sendIndex',(data)=>{
        if(data.index==-1&&data.now==-1)
        {
          this.active={
            title:"首页",
            content:[{title:''}]
          }
          this.index1='/index'
          this.now=-1
          this.icons="el-icon-s-home"
          this.$cookies.set("activeInfo",this.active)
          this.$cookies.set("index",this.index1)
          this.$cookies.set("now",this.now)
          this.$cookies.set("icon","el-icon-s-home")
        }
        else
        {
          this.index1 = data.index
          this.active = this.menu[data.index-1]
          this.now=data.now
          this.icons=this.active.icon
          this.$cookies.set("icon",this.active.icon)
          this.$cookies.set("activeInfo",this.active)
          this.$cookies.set("index",this.index1)
          this.$cookies.set("now",this.now)
        }
        
        // console.log(JSON.stringify(this.active)+'----')
      })
    },
    toHome(){
        this.active={
            title:"首页",
            content:[{title:''}]
          }
        this.index1='/index'
        this.now=-1
        this.icons="el-icon-s-home"

        this.$cookies.remove("activeInfo")
        this.$cookies.remove("index")
        this.$cookies.remove("now")
        this.$cookies.remove("icon")
    },
    init(){
      if(this.$cookies.get("activeInfo")!=null){
        this.active=this.$cookies.get("activeInfo")
        this.index1=this.$cookies.get("index")
        this.now=this.$cookies.get("now")
        this.icons=this.$cookies.get("icon")
      }else{
        if(this.active.length==0){
        this.active={
            title:"首页",
            content:[{title:''}]
          }
          this.index1='/index'
          this.now=-1
      }
      }

      
    }
  },
  created() {
    this.getIndex()
    this.init()
  },
  beforeDestroy() {
    // this.bus.$off('sendIndex') //销毁
  },
}
</script>

<style scoped>
#nav .el-breadcrumb {
  height: 60px;
  line-height: 60px;
  padding-left: 20px;
}
#nav .el-breadcrumb .title{
  font-weight: bold;
}
</style>



